.page-container {
  margin: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  outline: none;
}

.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 350px;
}

.game-header {
  display: flex;
  width: 100%;
  justify-content: space-between;
  margin-bottom: 24px;
  padding: 10px;
  box-sizing: border-box;
}

.title {
  background: #edc22e;
  color: #f9f6f2;
  width: 75px;
  height: 75px;
  font-size: 24px;
  border-radius: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
  margin: 0;
}

.controls {
  display: flex;
}

.info {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background: #d6cdc4;
  width: 75px;
  height: 75px;
  border-radius: 5px;
  color: #776e65;
  box-sizing: border-box;
  font-size: 16px;
  margin: 0 8px 0 0;
}

.button {
  border: none;
  border-radius: 5px;
  cursor: pointer;
  color: #f9f6f2;
  font-family: sans-serif;
  font-weight: 700;
  font-size: 16px;
  width: 75px;
  height: 75px;

  transition: 0.25s ease background;
}

.start {
  background: #1dae28;
  font-size: 20px;
  outline: none;

  &:hover {
    background: #179921;
  }
}

.restart {
  background: #f1b2b2;
  outline: none;

  &:hover {
    background: #f87474;
  }
}

.game-field {
  box-sizing: border-box;
  width: 300px;
  height: 300px;
  display: flex;
  flex-wrap: wrap;
  background: #bbada0;
  border-spacing: 10px;
  border-radius: 5px;
}

.message-container {
  width: 100%;
  height: 150px;
}

.message {
  box-sizing: border-box;
  width: 100%;
  background: #d6cdc4;
  color: #776e65;
  padding: 10px;
  text-align: center;
  border-radius: 5px;
  font-size: 20px;
}

.message-win {
  background: #edc22e;
  color: #f9f6f2;
}
